.body {
      width: 100%;
      height: calc(100% - 79px);
      overflow-y: auto;
      background-color: #0d1117;
}

.container {
      width: 100%;
      max-width: 900px;
      margin: 20px auto 100px auto;
      background-color: #222222;
      border-radius: 3px;
      padding: 0px;
      box-shadow: 0px 10px 20px 10px #000;
}

@media screen and (min-width: 910px) {
      .body {
            box-shadow: inset 0 0 20px 8px #000;
      }
      .container {
            padding: 15px;
      }
}