.header {
      /* height: 100px; */
      width: 100%;
      box-shadow: 0px 4px 20px 0px #000;
      background-color: #161b22;
}

.headHolder {
      border-bottom: 1px solid #000;
}

.head {
      height: 40px;
      min-width: 314px;
      max-width: 1000px;
      margin: 0 auto;
      padding: 0px 10px;
}

.logo {
      width: 36px;
      height: 36px;
      margin: 2px;
      cursor: pointer;
}

.heading {
      height: 100%;
      display: inline-block;
      line-height: 40px;
      vertical-align: top;
      margin-left: 5px;
      cursor: pointer;
}

.fileName {
      height: 30px;
      display: inline-block;
      line-height: 30px;
      vertical-align: top;
      margin: 5px 0px 5px 20px;
      max-width: 120px;
      overflow: hidden;
      word-break: break-all;
      cursor: text;
      padding: 0 8px;
      border-radius: 8px;
      color: rgb(218, 218, 218);
}

.fileName:hover {
      background-color: #3f3f3f7a;
}

.nameModals {
      max-width: 500px;
      width: 100%;
      min-width: 150px;
      margin: 10px;
      height: fit-content;
      border-radius: 8px;
      background-color: #161b22;
      position: relative;
      box-shadow: 0px 10px 15px 5px #000;
}

.nameModals_heading {
      padding: 20px 10px;
      border-bottom: 1px solid #222;
}

.nameModals_close {
      position: absolute;
      padding: 20px 20px;
      top: 0;
      right: 0;
      cursor: pointer;
}

.nameModals_body {
      padding: 10px;
}

.nameModals_text {
      font-size: 13px;
}

.nameModals_input {
      color: #111;
      width: 100%;
      padding: 5px 10px;
      margin-top: 5px;
      border: none;
      border-radius: 5px;
      outline: none;
      margin-bottom: 20px;
      box-shadow: 4px 4px 5px 3px #222;
}

.nameModals_btn {
      padding: 8px 10px;
      border-radius: 8px;
      font-size: 13px;
      background-color: #222;
      display: inline-block;
      margin: 2px;
      cursor: pointer;
}

.nameModals_btn:hover {
      box-shadow: inset 0 0 10px 2px #000;
}

.toolHolder {
      width: 100%;
}
.tool {
      width: 100%;
      max-width: 900px;
      margin: 0 auto;
      background-color: #161b22;
      border: 1px solid #000;
}

.tool_btn {
      margin: 3px;
      padding: 5px;
      display: inline-block;
      cursor: pointer;
      background-color: #222;
      border: 1px solid #000;
      border-radius: 2px;
}
.tool_btn:hover {
      box-shadow: inset 0 0 10px 2px rgba(255, 255, 255, 0.104);
}