.block {
      border: 1px solid #e0d9cc;
      padding: 5px;
      margin: 5px;
      /* background-color: #161b22; */
}

.head {
      padding: 5px;
}

.blockNumber {
      vertical-align: top;
      height: 100%;
}

.blockNumber a {
      text-decoration: none;
}

.blockDetails {
      display: block;
      width: 100%
}

.mdHolder {
      position: relative;
      border: 1px solid #111;
}

.md {
      width: 100%;
      height: auto;
      cursor: text;
      user-select: text;
      padding: 5px;
}

.md:hover~.mdEditBtn {
      opacity: 1;
}

.mdEditBtn {
      position: absolute;
      top: 1px;
      right: 1px;
      background-color: #333;
      border-radius: 5px;
      padding: 5px 8px;
      font-size: 13px;
      cursor: pointer;
      opacity: 0;
      transition: opacity 0.5s ease-in-out;
}

.mdEditBtn:hover {
      opacity: 1;
      box-shadow: inset 0 0 10px 2px #000;
}

.descModal {
      height: 100%;
      width: 100%;
      padding: 10px;
}

.descModal_div {
      height: 100%;
      width: 100%;
      background-color: #222;
      border-radius: 10px;
      padding: 10px;
      position: relative;
}

.descModal_heading {
      padding: 10px;
      border-bottom: 1px solid #000;
      /* min-width: 209px; */
      overflow: hidden;
      height: 43px;
}

.descModal_p {
      position: absolute;
      top: 24px;
      left: 220px;
      font-size: 12px;
      font-weight: 700;
}

@media (max-width: 400px) {
      .descModal_p {
            display: none;
      }
}

.descModal_close {
      position: absolute;
      padding: 20px 20px;
      top: 0;
      right: 0;
      cursor: pointer;
}

.descModal_text {
      display: inline-block;
      vertical-align: top;
      width: 50%;
      height: calc(100% - 43px);
      border: 1px solid #000;
}

.descModal_textarea {
      width: 100%;
      height: 100%;
      background-color: transparent;
      resize: none;
      outline: none;
      border: none;
      padding: 5px;
}

.descModal_md {
      display: inline-block;
      vertical-align: top;
      width: 50%;
      height: calc(100% - 43px);
      border: 1px solid #000;
      overflow: auto;
      padding: 5px;
}

@media screen and (max-width: 500px) {
      .descModal_text {
            display: block;
            width: 100%;
            height: calc(50% - 21px);
      }
      .descModal_md {
            display: block;
            width: 100%;
            height: calc(50% - 21px);
      }
}

